@import "../../index.scss";

footer {
  @include f-rules;
  flex-direction: column;
  padding-bottom: 25px;
  margin-top: 100px;
  gap: 10px;
  div {
    display: flex;
    gap: 8px;
    img {
      width: 40px;
      height: 40px;
      cursor: pointer;
      &:hover {
        @include filter;
      }
    }
  }
}
